<template>
  <v-card height="auto" style="background-color: rgb(0, 40, 50)">
    <v-tabs v-model="tab" dark background-color="rgb(0, 40, 50)">
      <v-tab class=""> Wallet Details </v-tab>
      <v-tab> Purchase Details </v-tab>
      <v-tab> Power Details </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item>
        <v-card
          class="text-center"
          v-if="walletList == ''"
          flat
          style="background-color: rgb(0, 40, 50)"
        >
          <v-card-text>
            <div class="pt-16"></div>
            <div class="pt-16"></div>
            <span class="text-h5">No Data</span>
            <div class="pt-16"></div>
            <div class="pt-16"></div>
          </v-card-text>
        </v-card>

        <v-card
          v-if="walletList != ''"
          flat
          style="background-color: rgb(0, 40, 50)"
        >
          <v-card-text>
            <v-card flat>
              <v-simple-table dark style="background-color: rgb(0, 40, 50)">
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left">Transport</th>
                      <th class="text-left">Wallet Address></th>
                      <th class="text-left">Cause</th>
                      <th class="text-left">ETH_Amt_In</th>
                      <th class="text-left">ETH_Amt_Out</th>
                      <th class="text-left">ETH_Amt_All</th>
                      <th class="text-left">Create_Time</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in walletList" :key="item.name">
                      <td>{{ item.Wallet[0] }}</td>
                      <td>{{ item.Wallet[1] }}</td>
                      <td>{{ item.Cause }}</td>
                      <td>{{ item.ETH_Amt_In }}</td>
                      <td>{{ item.ETH_Amt_Out }}</td>
                      <td>{{ item.ETH_Amt_All }}</td>
                      <td>{{ item.Create_Time }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-card>
            <div class="text-center">
              <v-container>
                <v-row justify="center">
                  <v-col cols="10">
                    <v-container class="max-width">
                      <v-pagination
                        v-model="walletPageLen"
                        class="my-4"
                        prev-icon="mdi-menu-left"
                        next-icon="mdi-menu-right"
                        :length="totalWalletPage"
                        @input="showPageWalletList()"
                      ></v-pagination>
                    </v-container>
                  </v-col>
                </v-row>
              </v-container>
            </div>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item>
        <v-card class="text-center" v-if="depositList == ''" flat>
          <v-card-text>
            <div class="pt-16"></div>
            <div class="pt-16"></div>
            <span class="text-h5">No Data</span>
            <div class="pt-16"></div>
            <div class="pt-16"></div>
          </v-card-text>
        </v-card>

        <v-card
          v-if="depositList != ''"
          flat
          style="background-color: rgb(0, 40, 50)"
        >
          <v-card-text>
            <v-row>
              <v-col>
                <v-card flat>
                  <v-simple-table dark style="background-color: rgb(0, 40, 50)">
                    <template v-slot:default>
                      <thead>
                        <tr>
                          <th class="text-left">Tid</th>
                          <th class="text-left">Cause</th>
                          <th class="text-left">Amt_In</th>
                          <th class="text-left">Amt_Out</th>
                          <th class="text-left">Amt_All</th>
                          <th class="text-left">Create_Time</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="item in depositList" :key="item.name">
                          <td>{{ item.Tid }}</td>
                          <td>{{ item.Cause }}</td>
                          <td>{{ item.Amt_In }}</td>
                          <td>{{ item.Amt_Out }}</td>
                          <td>{{ item.Amt_All }}</td>
                          <td>{{ item.Create_Time }}</td>
                        </tr>
                      </tbody>
                    </template>
                  </v-simple-table>
                </v-card>
                <div class="pt-8"></div>
              </v-col>
            </v-row>
          </v-card-text>
          <div class="text-center">
            <v-container>
              <v-row justify="center">
                <v-col cols="10">
                  <v-container class="max-width">
                    <v-pagination
                      v-model="depositPageLen"
                      class="my-4"
                      prev-icon="mdi-menu-left"
                      next-icon="mdi-menu-right"
                      :length="totalDepositPage"
                      @input="showPageDepositList()"
                    ></v-pagination>
                  </v-container>
                </v-col>
              </v-row>
            </v-container>
          </div>
        </v-card>
      </v-tab-item>
      <v-tab-item>
        <v-card class="text-center" v-if="powerList == ''" flat>
          <v-card-text>
            <div class="pt-16"></div>
            <div class="pt-16"></div>
            <span class="text-h5">No Data</span>
            <div class="pt-16"></div>
            <div class="pt-16"></div>
          </v-card-text>
        </v-card>

        <v-card
          v-if="powerList != ''"
          flat
          style="background-color: rgb(0, 40, 50)"
        >
          <v-card-text>
            <v-card flat>
              <v-simple-table dark style="background-color: rgb(0, 40, 50)">
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left">Group</th>
                      <th class="text-left">Position</th>
                      <th class="text-left">Deduction</th>
                      <th class="text-left">Create_Time</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in powerList" :key="item.name">
                      <td>{{ item.Group }}</td>
                      <td>{{ item.Position }}</td>
                      <td>{{ Number(item.Amt_Out).toFixed(2) }}</td>
                      <td>{{ item.Create_Time }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-card>
            <div class="text-center">
              <v-container>
                <v-row justify="center">
                  <v-col cols="10">
                    <v-container class="max-width">
                      <v-pagination
                        v-model="walletPageLen"
                        class="my-4"
                        prev-icon="mdi-menu-left"
                        next-icon="mdi-menu-right"
                        :length="totalWalletPage"
                        @input="showPageWalletList()"
                      ></v-pagination>
                    </v-container>
                  </v-col>
                </v-row>
              </v-container>
            </div>
          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>

<script>
import { depositList, dailList, powerList } from "../../api";

export default {
  data() {
    return {
      tab: null,
      depositList: "", //儲值明細
      walletList: "", //錢包明細
      powerList: "", //電費明細
      walletPageLen: 1, //錢包明細分頁頁數
      totalWalletPage: 1, //錢包明細分頁總頁數
      depositPageLen: 1, //儲值明細分頁頁數
      totalDepositPage: 1, //錢包明細分頁總頁數
      powerPageLen: 1, //儲值明細分頁頁數
      totalPowerPage: 1, //錢包明細分頁總頁數
    };
  },
  created() {
    this.getWalletList();
    this.getDepositList();
    this.getPowerList();
  },
  methods: {
    //先將資料列表的api做分頁處理，再引入api裡面的資料列表(錢包明細)
    getWalletList() {
      var params = new URLSearchParams();
      params.append("page", this.walletPageLen);
      params.append("number", 10);

      dailList(params)
        .then((result) => {
          //分頁邏輯處理
          let maxLength = 10;
          this.totalWalletPage = Math.ceil(
            parseInt(result.data.number) / maxLength
          );
          for (let index = 0; index <= this.totalWalletPage; index++) {
            if (index == 1) {
              this.walletPageLen = index;
              this.showPageWalletList();
              break;
            }
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //根據點選的頁數call出相對應的資料列表(錢包明細)
    showPageWalletList() {
      var params = new URLSearchParams();
      params.append("page", this.walletPageLen);
      params.append("number", 10);

      dailList(params)
        .then((result) => {
          this.walletList = result.data.list;
          this.walletList.forEach((element) => {
            element.Wallet = element.Wallet.split(",");
          });
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //先將資料列表的api做分頁處理，再引入api裡面的資料列表(儲值明細)
    getDepositList() {
      var params = new URLSearchParams();
      params.append("page", this.depositPageLen);
      params.append("number", 10);

      depositList(params)
        .then((result) => {
          let maxLength = 10;
          this.totalDepositPage = Math.ceil(
            parseInt(result.data.number) / maxLength
          );
          for (let index = 0; index <= this.totalDepositPage; index++) {
            if (index == 1) {
              this.depositPageLen = index;
              this.showPageDepositList();
              break;
            }
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //根據點選的頁數call出相對應的資料列表(儲值明細)
    showPageDepositList() {
      var params = new URLSearchParams();
      params.append("page", this.depositPageLen);
      params.append("number", 10);

      depositList(params)
        .then((result) => {
          this.depositList = result.data.list;
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //先將資料列表的api做分頁處理，再引入api裡面的資料列表(電費明細)
    getPowerList() {
      var params = new URLSearchParams();
      params.append("page", this.powerPageLen);
      params.append("number", 10);

      powerList(params)
        .then((result) => {
          let maxLength = 10;
          this.totalPowerPage = Math.ceil(
            parseInt(result.data.number) / maxLength
          );
          for (let index = 0; index <= this.totalPowerPage; index++) {
            if (index == 1) {
              this.powerPageLen = index;
              this.showPagePowerList();
              break;
            }
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },

    //根據點選的頁數call出相對應的資料列表(電費明細)
    showPagePowerList() {
      var params = new URLSearchParams();
      params.append("page", this.powerPageLen);
      params.append("number", 10);

      powerList(params)
        .then((result) => {
          this.powerList = result.data.list;
          console.log(this.powerList);
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
};
</script>